import { useEffect } from "react";
import Sketch from "react-p5";

let z = 0;
let p = [];
let subdivs = 30;
let speed = 4;
let scl = 200;
let bias = [0, 0];
let offset = [0, 0];
let aspect = [1, 1];
var cnv;

export function Terrain({ props }) {


  function curl(p5, vx, vy) {
    var eps = 0.0001;
    let x = vx;
    let y = vy;

    //Find rate of change in X direction
    var n1 = p5.noise(x + eps, y);
    var n2 = p5.noise(x - eps, y);

    //Average to find approximate derivative
    var a = (n1 - n2) / (2 * eps);

    //Find rate of change in Y direction
    var n1 = p5.noise(x, y + eps);
    var n2 = p5.noise(x, y - eps);

    //Average to find approximate derivative
    var b = (n1 - n2) / (2 * eps);

    //Curl
    return [b, -a];
  }

  function populateRect(w, h) {
    p = [];
    for (let y = 0; y < subdivs + 1; y++) {
      for (let x = 0; x < subdivs + 1; x++) {
        let tx = ((x * w) / subdivs) * aspect[0] + offset[0];
        let ty = ((y * h) / subdivs) * aspect[1] + offset[1];
        p.push([tx, ty, tx, ty, Math.random()]);
      }
    }
  }

  function populatePartLineX(w, h) {
    p = [];
    for (let y = 0; y < subdivs + 1; y++) {
      for (let x = 0; x < subdivs + 1; x++) {
        let tx = ((y * subdivs + x * w) / subdivs) * aspect[0] + offset[0];
        let ty = offset[1];
        p.push([tx, ty, tx, ty, Math.random()]);
      }
    }
  }

  function populatePartCircle(w, h) {
    p = [];
    for (let r = 0; r < subdivs; r++) {
      for (let a = 0; a < subdivs; a++) {
        let tx =
          Math.cos((a * Math.PI * 2) / subdivs) * ((r * h) / 2 / subdivs) +
          w / 2;
        let ty =
          Math.sin((a * Math.PI * 2) / subdivs) * ((r * h) / 2 / subdivs) +
          h / 2;
        p.push([tx, ty, tx, ty, Math.random()]);
      }
    }
  }

  function populateArray(w, h) {
    //population method
    //populatePartCircle(w, h);
    populateRect(w,h);
  }

  const setup = (p5) => {
    let parent = document.getElementById("p5CanvasParent");
    cnv = p5
      .createCanvas(parent.clientWidth, parent.clientHeight)
      .parent(parent);
    populateArray(p5.width, p5.height);
    z=0;
  };

  const resize = (p5) => {
    let parent = document.getElementById("p5CanvasParent");
    populateArray(parent.clientWidth, parent.clientHeight);
    cnv = p5
      .createCanvas(parent.clientWidth, parent.clientHeight)
      .parent(parent);
      z=0;
  };

  function drawPoints(p5){
    for (let index = 0; index < p.length; index++) {
        let px = p[index][0];
        let py = p[index][1];
        let tx = p[index][2];
        let ty = p[index][3];
  
        let col = p5.color(`rgba(216, 208, 207,${z})`);
        if (p[index][4] > 0.5) {
          col = p5.color(`rgba(31, 153, 153,${z})`);
        }
  
        //display point
        p5.stroke(col);
        p5.point(tx,ty);
  
        let curlAtPoint = curl(p5, tx / scl, ty / scl);
        //add noise velocity to point
        tx += curlAtPoint[0] * speed + bias[0];
        ty += curlAtPoint[1] * speed + bias[1];
        //handle wraparound
        ty -= p5.map(ty,0,p5.height,-speed,speed);
        tx -= p5.map(tx,0,p5.width,-speed,speed);
  
        //set new values
        p[index][0] = p[index][2];
        p[index][1] = p[index][3];
        p[index][2] = tx;
        p[index][3] = ty;
      }
    if(z<1){z+=0.01}
  };

  const draw = (p5) => {
    drawPoints(p5);
  };

  useEffect(() => {
    
  }, []);

  return <Sketch windowResized={resize} setup={setup} draw={draw} />;
}
