import { useState, useEffect } from "react";
import { langList, langCaps, bookList, bookCaps } from "../images/images";
import PicList from "../components/PicList";
import { faceAnim } from "../images/images";
import { motion } from "framer-motion";

function About({ updateText }) {
  var flavorText = {
    title: "ABOUT ME",
    subtitle: "WHO AM I",
  };

  useEffect(() => {
    updateText(flavorText);
    updateText(flavorText);
    console.log("About");
  }, []);

  const [frame, setFrame] = useState(0);
  useEffect(() => {
    const frameInterval = setInterval(() => {
      setFrame((f) => (f + 1) % 64);
    }, 100);

    return () => clearInterval(frameInterval);
  }, []);

  return (
    <motion.div
      className="inner"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      exit={{ opacity: 0 }}
    >
      <br />
      <center>
        <div className="inlineDiv" style={{ width: "min(400px, 100%)" }}>
          <img
            src={faceAnim[frame]}
            style={{ maxWidth: "min(40vh, 75vw)" }}
            alt="Franco in Courier Bold"
          />
        </div>

        <div
          className="inlineDiv"
          style={{ width: "min(600px, 100%)", textAlign: "left" }}
        >
          <h1 style={{ fontSize: "500%" }}>Hi, I'm Franco.</h1>
          <p style={{ fontSize: "150%" }}>
            I'm a Software Engineer interested in Web Design, Mathematics, and Creative Coding.
          </p>
        </div>
      </center>
    </motion.div>
  );
}

export default About;
